import React, {Component} from 'react';
import "./Contacts.css";
import Page from "../Page/Page";
import Section from "../Section/Section";
import {Link} from 'react-router-dom';

export default class Documents extends Component {
    state = {
        contacts: []
    }

    constructor(props, context) {
        super(props, context);

        this.state.contacts.push({
            letter: 'A',
            peoples: [{
                name: 'Alain CARADEC',
                telephone: '06 52 11 18 26',
                email: 'a.caradec@2a-services.fr'
            }, {
                name: 'Alain CARADEC',
                telephone: '06 52 11 18 26',
                email: 'a.caradec@2a-services.fr'
            }]
        }, {
            letter: 'B',
            peoples: [{
                name: 'Beatrice CARADEC',
                telephone: '06 52 11 18 26',
                email: 'a.caradec@2a-services.fr'
            }]
        }, {
            letter: 'C',
            peoples: [{
                name: 'Carl CARADEC',
                telephone: '06 52 11 18 26',
                email: 'a.caradec@2a-services.fr'
            }, {
                name: 'Ciril CARADEC',
                telephone: '06 52 11 18 26',
                email: 'a.caradec@2a-services.fr'
            }, {
                name: 'Cedric CARADEC',
                telephone: '06 52 11 18 26',
                email: 'a.caradec@2a-services.fr'
            }]
        })
    }

    render() {
        return (
            <Page title="Contacts" className="contacts" actions={[(
                <Link to="" className="button button-default">
                    <i className="ion-ios-plus-empty"></i> AJOUTER UN CONTACT
                </Link>
            ), (
                <Link to="" className="button button-stable button-icon">
                    <i className="ion-more"></i>
                </Link>
            )]}>
                <Section title="25 contacts">
                    {this.state.contacts.map((group, key) => (
                        <div key={key} className="contact-group">
                            <h1>{group.letter}</h1>
                            {group.peoples.map((item, key) => (
                                <div key={key} className="contact">
                                    <input className="contact-checkbox" type="checkbox"/>
                                    <i className="contact-icon ion-ios-person-outline"></i>
                                    <span className="contact-name">{item.name}</span>
                                    <span className="contact-telephone">{item.telephone}</span>
                                    <span className="contact-email">{item.email}</span>
                                </div>
                            ))}
                        </div>
                    ))}
                </Section>
            </Page>
        );
    }
}


