import React, {Component} from 'react';
import "./Documents.css";
import PropTypes from 'prop-types';
import Page from "../Page/Page";
import Section from "../Section/Section";
import {Link} from 'react-router-dom';
import classNames from 'classnames';
import ToolBoxOptions from '../components/ToolBoxOptions/ToolBoxOptions';
import LabeLicon from '../components/LabeLicon/LabeLicon';

class Document extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            selected: this.props.item.selected || false
        };
    }

    _getItemIcon(item) {
        var type = item.attributes.type || 'file';
        var icon = (type == 'file') ? 'file' : 'folder';
        var tags = item.attributes.tags || [];

        tags.forEach((item) => {
            if ((/^folder-/).test(item)) {
                icon = item;
            }
        });
        return icon;
    }


    _onItemClicked(e, item) {
        item.selected = !item.selected;
        this.setState({item: item});
        if (_.isFunction(this.props.onItemClicked))
            this.props.onItemClicked(e, item);
    }

    _onOpenItem(e, item) {
        if (_.isFunction(this.props.onOpenItem))
            this.props.onOpenItem(e, item);
        return false;
    }

    render() {
        var item = this.props.item;
        return (
            <div
                onClick={(e) => this._onOpenItem(e, item)}
                className={classNames({"document": true, 'selected': item.selected})}
            >
                <input
                    ref="checkbox"
                    checked={item.selected}
                    type="checkbox"
                    onClick={(e) => {
                        e.stopPropagation();
                        item.selected = !item.selected;
                        if (_.isFunction(this.props.onItemSelected)) {
                            this.props.onItemSelected(item);
                        }
                    }}
                />

                <div className="document-icon-container">
                    <div className="document-icon-content">
                        <img
                            src={item.icon || "/images/" + this._getItemIcon(item) + (item.selected ? '.selected' : '') + ".png"}
                            className="document-icon"
                        ></img>
                    </div>
                </div>

                <div
                    className="document-name"
                >{item.attributes.name}</div>

                <div className="document-date">Dernière
                    modification&nbsp;:&nbsp;{moment(item.attributes.updated_at).format('DD/MM/YYYY')}</div>
            </div>
        );
    }
}
export default class Documents extends Component {


    constructor(props, context) {
        super(props, context);
        var path = '/Documents' + (this.props.match.params.path || '');


        this.state = {
            isRoot: path == '/Documents',
            documents: [],
            path: path,
            viewMode: localStorage.documentsViewMode || false
        };

        cozy.client
            .files
            .statByPath('/Documents')
            .catch(() => {
                console.log('Creating Documents');
                return cozy.client
                    .files
                    .createDirectory({
                        name: 'Documents',
                        lastModified: new Date()
                    }).then((result) => {
                        return Promise.all([
                            cozy.client.files
                                .createDirectory({
                                    name: 'Équipements',
                                    tags: ['folder-equipements'],
                                    dirID: result._id
                                }),

                            cozy.client.files
                                .createDirectory({
                                    name: 'Équipements',
                                    tags: ['folder-equipements'],
                                    dirID: result._id
                                }),

                            cozy.client.files
                                .createDirectory({
                                    name: 'Documents de propriété',
                                    tags: ['folder-document-de-propriete'],
                                    dirID: result._id
                                }),

                            cozy.client.files
                                .createDirectory({
                                    name: 'Travaux et entretien',
                                    tags: ['folder-travaux-et-entretien'],
                                    dirID: result._id
                                })
                        ]);
                    }).then(() => this.loadDocuments(this.state.path));
            });

        Object.defineProperty(this, 'allSelected', {
            get: () => {
                var selected = true;
                this.state.documents.forEach((item) => {
                    selected = selected && item.selected;
                });
                return selected;
            },
            set: (val) => {
                this.state.documents.forEach((item) => {
                    item.selected = val;
                });
                return val;
            }
        });
    }

    componentWillMount() {
        this.loadDocuments(this.state.path);
        this.context.router.history.listen((e) => {
            this.loadDocuments(e.pathname);
        });
    }

    componentDidMount() {

    }

    loadDocuments(path) {
        cozy.client
            .files
            .statByPath(path)
            .then((result) => {
                var documents = [];

                result
                    .relations('contents')
                    .forEach((item) => {
                        documents.push(item);
                    });


                this.setState({
                    path: path,
                    documents: documents,
                    isRoot: (path == '/Documents')
                });
            });
    }

    getNbSelected() {
        var selectedCount = 0;
        this.state.documents.forEach((item) => {
            if (item.selected)
                selectedCount++;
        });
        return selectedCount;
    }

    onItemClicked(e, item) {
    }

    onItemSelected(item) {
        this.setState({});
    }

    openItem(e, item) {
        e.stopPropagation();


        if (item.attributes.type != 'file')
            this.context.router.history.push(item.attributes.path);
    }


    onBackPress() {
        var folders = this.state.path.split('/');
        if (folders.length > 1) {
            folders = folders.slice(0, folders.length - 1);
        }

        this.context.router.history.push(folders.join('/'));
    }


    importFile() {
        this.refs.file.click();
    }

    getCurrentDir() {
        return cozy
            .client
            .files
            .statByPath(this.state.path)
            .then((result) => this.currentDirState = result);
    }

    onFileChange(e) {

        this.getCurrentDir().then((currentDirState) => {
            Array.from(this.refs.file.files).forEach((file) => {
                var document = {
                    attributes: {
                        name: file.name
                    }
                };

                this.state.documents.push(document);

                this.setState({
                    documents: this.state.documents
                });

                window
                    .cozy
                    .client
                    .files
                    .create(file, {
                        dirID: currentDirState._id
                    })
                    .then((result) => {
                        this.state.documents.forEach((item) => {
                            if (item.attributes.name == document.attributes.name)
                                Object.assign(item, result);
                        });
                    });
            });
        });
    }

    createDirectory() {
        var name = window.prompt('Nom du dossier à créer');
        if (name && name.trim().length > 0) {
            this.getCurrentDir().then((currentDirState) => {

                var document = {
                    attributes: {
                        name: name,
                        type: 'directory'
                    }
                };

                this.state.documents.push(document);

                this.setState({
                    documents: this.state.documents
                });

                window
                    .cozy
                    .client
                    .files
                    .createDirectory({
                        name: name,
                        dirID: currentDirState._id,
                        lastModifiedDate: new Date()
                    })
                    .then((result) => {
                        this.state.documents.forEach((item) => {
                            if (item.attributes.name == document.attributes.name)
                                Object.assign(item, result);
                        });
                    })
            })
        }
    }


    removeSelectedFiles() {
        if (confirm('Voulez-vous vraiment supprimer ces fichiers?')) {
            var all = [];
            this.state.documents.forEach((item, index) => {
                if (item.selected) {
                    this.state.documents.splice(index, 1);
                    cozy.client.files.trashById(item._id);
                }
            });
            this.setState({
                documents: this.state.documents
            });
        }
    }

    downloadSelected() {
        this.state.documents.forEach((item) => {

            if (item.selected) {
                var link = cozy
                    .client
                    .files
                    .getDownloadLinkById(item._id)
                    .then((url) => {
                        console.log(item);
                        setTimeout(() => {
                            window.open(url, '_blank');
                        }, 100);
                    });
            }

        });
    }

    render() {

        var nbSelected = this.getNbSelected();


        return (
            <Page title="Documents" className="documents" actions={[nbSelected > 0 ? (

                <button type="button" onClick={() => this.downloadSelected()} className="button button-stable">
                    <i className="ion-ios-cloud-download-outline"></i> TELECHARGER
                </button>
            ) : null, nbSelected > 0 ? (
                <button type="button" onClick={() => this.removeSelectedFiles()} className="button button-assertive">
                    <i className="ion-ios-trash-outline"></i> SUPPRIMER
                </button>
            ) : null,
                (
                    <button type="button" onClick={() => this.importFile()}
                            className="button button-default button-import">
                        <input ref="file" type="file" onChange={(e) => this.onFileChange(e)} multiple="true"/>
                        <i className="ion-ios-upload-outline"></i> IMPORTER UN DOCUMENT
                    </button>
                ), (
                    <ToolBoxOptions>
                        <ul>
                            <li onClick={() => {
                                var viewMode = !this.state.viewMode;
                                localStorage.documentsViewMode = viewMode;
                                this.setState({viewMode: viewMode});
                            }}>
                                {this.state.viewMode ? (<LabeLicon icon="ion-navicon">VUE LISTE</LabeLicon>) : (
                                    <LabeLicon icon="ion-grid">VUE GRILLE</LabeLicon>)}
                            </li>
                            <li onClick={() => this.createDirectory()}>
                                <LabeLicon icon="ion-ios-plus-empty">NOUVEAU DOSSIER</LabeLicon>
                            </li>
                            <li>
                                <LabeLicon icon="ion-ios-cloud-download-outline">TOUT TELECHARGER</LabeLicon>
                            </li>
                        </ul>
                    </ToolBoxOptions>

                )]} backEnabled={!this.state.isRoot} onBackPress={() => this.onBackPress()}>
                <Section title={ nbSelected > 0 ? (nbSelected + " élément" + (nbSelected == 1 ? "" : "s")) : (
                    <span>&nbsp;</span>)}>
                    {this.state.documents.length > 0 ?
                        (
                            <div className={classNames({
                                "document-list": true,
                                "view-mode-grid": this.state.viewMode,
                                "view-mode-line": !this.state.viewMode
                            })}>
                                {this.state.documents.map((item, key) => (
                                    <Document
                                        key={key}
                                        item={item}
                                        onItemSelected={(item) => this.onItemSelected(item)}
                                        onOpenItem={(e, item) => this.openItem(e, item)}
                                        onItemClicked={(e, item) => this.onItemClicked(e, item)}
                                    />
                                ))}
                            </div>
                        )
                        :
                        (

                            <div className="text-center padding">Le dossier est vide</div>
                        )
                    }
                </Section>
            </Page>
        );
    }
}

Documents.contextTypes = {
    router: PropTypes.shape({
        history: PropTypes.shape({
            push: PropTypes.func.isRequired,
            replace: PropTypes.func.isRequired,
            createHref: PropTypes.func.isRequired
        }).isRequired
    }).isRequired
};




