import React, {Component} from 'react';
import "./ToolBoxOptions.css";
import classNames from 'classnames';
import _ from 'lodash';

export default class ToolBoxOptions extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            open: false
        };
    }

    componentDidMount() {
        this.bodyClickEvent = this.blur.bind(this);
        window.$('body').on('click', this.bodyClickEvent);
    }

    componentWillUnmount() {
        window.$('body').off('click', this.bodyClickEvent);
    }

    blur() {
        this.close();
    }

    open() {
        if(this.state.open)
            return;

        this.setState({
            open: true
        });
        this._onOpen();
    }

    close() {
        if(!this.state.open)
            return;
        
        this.setState({
            open: false
        });
        this._onClose();
    }

    componentWillReceiveProps(props, context) {
        var lastOpen = this.state.open;
        this.setState({
            open: props.open || false
        });

        if (lastOpen != (props.open || false)) {
            if (lastOpen)
                this._onClose();
            else
                this._onOpen();
        }
    }

    _onClose() {
        if (_.isFunction(this.props.onClose)) {
            this.props.onClose();
        }
    }

    _onOpen() {
        if (_.isFunction(this.props.onOpen)) {
            this.props.onOpen();
        }
    }

    toggle() {
        if (this.state.open)
            this.close();
        else
            this.open();
    }

    render() {
        return (
            <div className={classNames({"toolBox": true, "open": this.state.open})}>
                <button type="button" onClick={() => this.toggle()} className="button button-stable button-icon">
                    <i className="ion-more"></i>
                </button>

                <div className="toolBox-content">
                    <div className="toolBox-content-triangle"></div>
                    {this.props.children}
                </div>
            </div>
        )
    }
}


