import React, {Component} from 'react';
import "./Navigation.css";
import {Link} from 'react-router-dom'
import PropTypes from 'prop-types';
import classNames from 'classnames';

export default class Navigation extends Component {

    constructor(props, context) {
        super(props, context);

        console.log(context);

        this.state = {
            route: this.context.router.route.location
        }
    }

    componentWillMount() {
        this.context.router.history.listen((e) => {
            this.setState({
                route: e
            });
        });
    }

    render() {
        return (
            <aside className="navigation">
                <nav>
                    <ul>
                        <li className={classNames({"active": (/^\/Documents/).test(this.state.route.pathname)})}>
                            <Link to="/Documents"><i className="ion-ios-folder"></i>Documents</Link>
                        </li>
                        <li className={classNames({"active": this.state.route.pathname == '/donnees'})}>
                            <Link to="/donnees"><i className="ion-ios-paper-outline"></i>Données</Link>
                        </li>
                        <li className={classNames({"active": this.state.route.pathname == '/rappels'})}>
                            <Link to="/rappels"><i className="ion-ios-checkmark-outline"></i>Rappels</Link>
                        </li>
                        <li className={classNames({"active": this.state.route.pathname == '/contacts'})}>
                            <Link to="/contacts"><i className="ion-ios-bookmarks-outline"></i>Contacts</Link>
                        </li>
                    </ul>
                </nav>

                <nav>
                    <ul>
                        <li className={classNames({"active": this.state.route.pathname == '/notifications'})}>
                            <Link to="/notifications">Notifications</Link>
                        </li>
                        <li className={classNames({"active": this.state.route.pathname == '/preferences'})}>
                            <Link to="/preferences">Préférences</Link>
                        </li>
                        <li className={classNames({"active": this.state.route.pathname == '/aide'})}>
                            <Link to="/aide">Aide</Link>
                        </li>
                        <li className={classNames({"active": this.state.route.pathname == '/a-propos'})}>
                            <Link to="/a-propos">À propos</Link>
                        </li>
                    </ul>
                </nav>
            </aside>
        );
    }
}

Navigation.contextTypes = {
    router: PropTypes.shape({
        history: PropTypes.shape({
            push: PropTypes.func.isRequired,
            replace: PropTypes.func.isRequired,
            createHref: PropTypes.func.isRequired
        }).isRequired
    }).isRequired
};


