import React, {Component} from 'react';
import "./Notes.css";
import Page from "../Page/Page";
import Section from "../Section/Section";
import {Link} from 'react-router-dom';

export default class Notes extends Component {


    constructor(props, context) {
        super(props, context);
        this.state = {
            notes: [{
                title: 'Renouveler garantie',
                date: '11/04/2017'
            }, {
                title: 'Vérifier chaudière',
                date: '11/04/2017'
            }, {
                title: 'Refaire toiture',
                date: '11/04/2017'
            }]
        }
    }

    render() {
        return (
            <Page title="Rappels" className="notes" actions={[(
                <Link to="" className="button button-default">
                    <i className="ion-ios-plus-empty"></i> AJOUTER UN RAPPEL
                </Link>
            ), (
                <Link to="" className="button button-stable button-icon">
                    <i className="ion-more"></i>
                </Link>
            )]} backEnabled={true}>
                <Section title="3 rappels" options={(
                    <div>TRIER PAR CREATION</div>
                )}>
                    {this.state.notes.map((note, key) => (
                        <div key={key} className="note">
                            <input className="note-checkbox" type="checkbox"/>
                            <span className="note-title">{note.title}</span>
                            <span className="note-date">Echéance : {note.date}</span>
                        </div>

                    ))}
                </Section>
            </Page>
        );
    }
}


