import React, {Component} from 'react';
import './Content.css';

export default class Content extends Component {
    constructor(props, context) {
        super(props, context);
    }

    render() {
        return (
            <div className="main-content">
                <div className="main-content-container">
                    {this.props.children}
                </div>
            </div>
        );
    }
}


