import React, {Component} from 'react';
import classNames from 'classnames';
import _ from 'lodash';

export default class SearchInput extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            active: false
        };
    }

    _onFocus() {
        this.setState({
            active: true
        });
    }

    _onBlur() {
        this.setState({
            active: false
        });
    }

    _onChange() {
        this.setState({
            value: this.refs.input.value
        });
    }

    _onDelete() {
        this.refs.input.value = '';
        this._onChange();
    }

    componentWillReceiveProps(props) {
        if (!_.isUndefined(props.active)) {
            this.setState({
                active: props.active
            });
        }
    }

    render() {
        return (
            <div className={classNames({input: true, 'input-active': this.state.active})}>
                <i className="ion-ios-search-strong"></i>
                <input
                    onChange={() => this._onChange()}
                    ref="input"
                    onFocus={() => this._onFocus()}
                    onBlur={() =>this._onBlur()}
                    type="text"
                    placeholder="Rechercher"
                />
                {this.refs.input && this.refs.input.value && this.refs.input.value.length > 0 && (
                    <i className="ion-ios-close-empty delete-icon" onClick={() => this._onDelete()}></i>)}
            </div>
        );
    }
}


