import React, {Component} from 'react';
import "./Page.css";
import {browserHistory} from 'react-router-dom';
import _ from 'lodash';

export default class Page extends Component {
    constructor(props, context) {
        super(props, context);
    }

    _goBack() {
        if (_.isFunction(this.props.onBack))
            this.props.onBack();

        if (_.isFunction(this.props.onBackPress))
            this.props.onBackPress();
        else
            browserHistory.goBack();
    }

    render() {
        return (
            <div className={"page " + (this.props.className || "")}>
                <div className="page-header">
                    {this.props.backEnabled && (
                        <button className="page-back-button" onClick={() => this._goBack()}>
                            <i className="ion-ios-arrow-thin-left"></i>
                        </button>
                    )}
                    <h1>{this.props.title}</h1>
                    {this.props.actions && (
                        <div className="page-options">
                            <ul className="page-actions">
                                {this.props.actions.map((item, index) => {
                                    return (
                                        <li key={index}>
                                            {item}
                                        </li>
                                    )
                                })}
                            </ul>
                        </div>
                    )}
                </div>
                <div className="page-content">
                    {this.props.children}
                </div>
            </div>
        );
    }
}


