import React, {Component} from 'react';
import classNames from 'classnames';
import _ from 'lodash';
import "./Collapsable.css";

export default class Collapsable extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            collapsed: _.isUndefined(props.collapsed) ? true : props.collapsed
        };
    }

    toggle() {
        this.setState({
            collapsed: !this.state.collapsed
        });
    }

    componentWillReceiveProps(props, context) {
        this.setState({
            collapsed: _.isUndefined(props.collapsed) ? true : props.collapsed
        });
    }

    render() {
        return (
            <article className={classNames({collapsable: true, collapse: this.state.collapsed})}>
                <div className="collapsable-header" onClick={() => this.toggle()}>
                    <i className="ion-ios-arrow-down"></i>
                    <h1 className="collapsable-title">{this.props.title}</h1>
                </div>
                <div className="collapsable-content">
                    {this.props.children}
                </div>
            </article>
        )
    }
}


