import React, {Component} from 'react';
import "./Data.css";
import Page from "../Page/Page";
import Section from "../Section/Section";
import {Link} from 'react-router-dom';

export default class Data extends Component {


    constructor(props, context) {
        super(props, context);

        this.state = {
            dataList: [{
                name: 'Diagnostics',
                icon: ''
            }, {
                name: 'Données de propriété',
                icon: ''
            }, {
                name: 'Équipements',
                icon: ''
            }, {
                name: 'Travaux et entretien',
                icon: ''
            }, {
                name: 'Diagnostics',
                icon: ''
            }, {
                name: 'Données de propriété',
                icon: ''
            }, {
                name: 'Équipements',
                icon: ''
            }, {
                name: 'Travaux et entretien',
                icon: ''
            }]
        }
    }

    render() {
        return (
            <Page title="Données" className="dataList">
                <Section title="3 éléments" options={(
                    <div>TRIER PAR NOM</div>
                )}>
                    <div className="data-list">
                        {this.state.dataList.map((item, key) => (
                            <div key={key} className="data">
                                <div className="data-icon-container">
                                    <div className="data-icon"></div>
                                </div>
                                <div className="data-name">{item.name}</div>
                            </div>
                        ))}
                    </div>
                </Section>
            </Page>
        );
    }
}


