import React, {Component} from 'react';
import "./Notifications.css";
import Page from "../Page/Page";
import Section from "../Section/Section";
import {Link} from 'react-router-dom';

export default class Documents extends Component {


    constructor(props, context) {
        super(props, context);

        state = {
            notifications: [{
                title: 'M.F.C a ajouté dans votre HOMEBOOK le document «Guide d’accueil du logement».',
                subtitle: 'Documents - Documents de propriété',
                date: '11/04/2017',
                img: null
            }, {
                title: 'M.F.C a ajouté dans votre HOMEBOOK le document «Guide d’accueil du logement».',
                subtitle: 'Documents - Documents de propriété',
                date: '11/04/2017',
                img: null
            }, {
                title: 'M.F.C a ajouté dans votre HOMEBOOK le document «Guide d’accueil du logement».',
                subtitle: 'Documents - Documents de propriété',
                date: '11/04/2017',
                img: null
            }]
        };
    }

    render() {
        return (
            <Page title="Notifications" className="notifications">
                <Section title="1 nouvelles notification">
                    {this.state.notifications.map((item, key) => (
                        <article key={key} className="notification">
                            <img className="notification-image" src={item.img} alt={item.title}/>
                            <div className="notification-content">
                                <h1>{item.title}</h1>
                                <h2>{item.subtitle}</h2>
                                <p>{item.date}</p>
                            </div>
                        </article>
                    ))}
                </Section>
            </Page>
        );
    }
}


