import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';

import {BrowserRouter as Router, Route, IndexRoute, Switch, Redirect} from 'react-router-dom';
import Header from './Header/Header';
import Footer from './Footer/Footer';
import Navigation from './Navigation/Navigation';
import Content from './Content/Content';
import Documents from './Documents/Documents';
import Data from './Data/Data';
import Notes from './Notes/Notes';
import Contacts from './Contacts/Contacts';
import Notifications from './Notifications/Notifications';
import Preferences from './Preferences/Preferences';
import Help from './Help/Help';
import About from './About/About';

export default class App extends Component {
    constructor(props, context) {
        super(props, context);
        if (window.cozy) {

            const app = document.querySelector('[role="application"]');

            var options, client1, client2;
            options = {
                cozyURL: `//${app.dataset.cozyStack}`,
                token: app.dataset.token
            };

            // Appel direct
            cozy.client.init(options);
            /*
             cozy.client.files.statById("io.cozy.files.root-dir").then((result) => {
             result.relations('contents').forEach((item) => {
             console.log('item : ', item);
             })
             });
             */
        }
    }

    componentDidMount() {
    }

    render() {
        return (
            <Router>
                <div>
                    <Header></Header>
                    <Navigation></Navigation>
                    <Content>
                        <Switch>
                            <Redirect from="/" to="/Documents/"/>
                            <Route path="/Documents/:path*" component={Documents} type="switch"/>
                        </Switch>
                        <Route path="/Documents/:path*" component={Documents} type="switch"/>
                        <Route path="/donnees" component={Data} type="switch"/>
                        <Route path="/rappels" component={Notes} type="switch"/>
                        <Route path="/contacts" component={Contacts} type="switch"/>

                        <Route path="/notifications" component={Notifications} type="switch"/>
                        <Route path="/preferences" component={Preferences} type="switch"/>
                        <Route path="/aide" component={Help} type="switch"/>
                        <Route path="/a-propos" component={About} type="switch"/>
                    </Content>
                    <Footer></Footer>
                </div>
            </Router>
        );
    }
}


